import { NextResponse } from "next/server";
import prisma from "@/lib/prisma";
import { getUserFromCookie } from "@/lib/auth";

export async function GET(req, { params }) {
  try {
    const user = await getUserFromCookie(req);
    if (!user) return NextResponse.json({ error: "Unauthorized" }, { status: 401 });

    const { friendId } = params;
    const url = new URL(req.url);
    const afterId = url.searchParams.get("afterId");

    let where = {
      OR: [
        { senderId: user.id, receiverId: friendId },
        { senderId: friendId, receiverId: user.id },
      ],
    };

    // لو فيه afterId → نجيب الرسائل الجديدة فقط
    if (afterId) {
      const lastMessage = await prisma.message.findUnique({ where: { id: afterId } });
      if (lastMessage) {
        where = {
          AND: [
            {
              OR: [
                { senderId: user.id, receiverId: friendId },
                { senderId: friendId, receiverId: user.id },
              ],
            },
            { createdAt: { gt: lastMessage.createdAt } },
          ],
        };
      }
    }

    const messages = await prisma.message.findMany({
      where,
      orderBy: { createdAt: "asc" },
    });

    return NextResponse.json(messages);
  } catch (err) {
    console.error("Fetch messages error:", err);
    return NextResponse.json({ error: "Failed to fetch messages" }, { status: 500 });
  }
}
